import React from 'react';
import ReactDOM from 'react-dom';
import anime from 'animejs';

import App from './app';
import './main.css';

ReactDOM.render(
    <div>
        <App />
    </div>,
    document.querySelector('#root')
);

anime({
    targets: '.firstName path',
    strokeDashoffset: [anime.setDashoffset, 0],
    easing: 'easeInOutSine',
    duration: 1500,
    delay: function(el, i) { return (i+1) * 200 },
    direction: 'alternate',
    loop: false
  });

  anime({
    targets: '.lastName path',
    strokeDashoffset: [anime.setDashoffset, 0],
    easing: 'easeInOutSine',
    duration: 1500,
    delay: function(el, i) { return (i+1) * 280 },
    direction: 'alternate',
    loop: false
  });
