import React, {useState} from 'react';

import './pyramid.css';


const Projects = () =>{
    const [selected, setSelected] = useState(3);
    const selectOrder = ["weather", "chess", "forum", "projectsmain", "weather", "chess", "forum", "projectsmain", "weather"];

    let handleClick = (direction) =>{
        if (direction === "left"){
            if (selected === 6){
                setSelected(3);     
            }
            else {
                setSelected(selected+1);
            }
            document.getElementById(selectOrder[selected]).style.left = "calc(45vw - 150px - 150px)";
            document.getElementById(selectOrder[selected]).style.width = "150px";
            document.getElementById(selectOrder[selected]).style.height = "100px";
            document.getElementById(selectOrder[selected]).style.top = "150px";
            document.getElementById(selectOrder[selected]).style.zIndex = "10";


            document.getElementById(selectOrder[selected+1]).style.left = "calc(45vw - 150px)";
            document.getElementById(selectOrder[selected+1]).style.width = "300px";
            document.getElementById(selectOrder[selected+1]).style.height = "200px";
            document.getElementById(selectOrder[selected+1]).style.top = "100px";
            document.getElementById(selectOrder[selected+1]).style.zIndex = "15";
           

            document.getElementById(selectOrder[selected+2]).style.opacity = "1";
            document.getElementById(selectOrder[selected+2]).style.left = "calc(45vw + 150px)";
            document.getElementById(selectOrder[selected+2]).style.zIndex = "9";

            document.getElementById(selectOrder[selected-1]).style.opacity = "0";
            document.getElementById(selectOrder[selected-1]).style.zIndex = "1";
            document.getElementById(selectOrder[selected-1]).style.left = "calc(45vw - 75px)";
            document.getElementById(selectOrder[selected-1]).style.top = "150px";
        }
        else{
            if (selected === 3){
                setSelected(6);
 
            }
            else {
                setSelected(selected-1);
            }
            document.getElementById(selectOrder[selected]).style.left = "calc(45vw + 150px)";
            document.getElementById(selectOrder[selected]).style.width = "150px";
            document.getElementById(selectOrder[selected]).style.height = "100px";
            document.getElementById(selectOrder[selected]).style.top = "150px";
            document.getElementById(selectOrder[selected]).style.zIndex = "10";


            document.getElementById(selectOrder[selected-1]).style.left = "calc(45vw - 150px)";
            document.getElementById(selectOrder[selected-1]).style.width = "300px";
            document.getElementById(selectOrder[selected-1]).style.height = "200px";
            document.getElementById(selectOrder[selected-1]).style.top = "100px";
            document.getElementById(selectOrder[selected-1]).style.zIndex = "15";


            document.getElementById(selectOrder[selected-2]).style.opacity = "1";
            document.getElementById(selectOrder[selected-2]).style.left = "calc(45vw - 150px - 150px)";
            document.getElementById(selectOrder[selected-2]).style.zIndex = "9";

            document.getElementById(selectOrder[selected+1]).style.opacity = "0";
            document.getElementById(selectOrder[selected+1]).style.zIndex = "1";
            document.getElementById(selectOrder[selected+1]).style.left = "calc(45vw - 75px)";
            document.getElementById(selectOrder[selected+1]).style.top = "150px";
        }
    }

    let layers = () =>{
        return (<div id = "projectlayers">
            
            <i onClick = {() => handleClick("left")} className ="big icon angle left leftarrow"/>
            
            <div id = "projectsmain" className={selected !== 3 ? `responsive__hidesidebox` : ""}>
                <h3>My Personal Projects</h3>
                <h4>These projects are built with a react and redux front-end with a nodejs backend</h4>
            </div>

            <a rel="noreferrer" href = "https://simpchess.com/weatherapp" target="_blank" id = "weather" className={selected !== 4 ? `responsive__hidesidebox` : ""}>
                <h3>Weather Application</h3>
                <h4>Using the open weather map api, I designed a 5 day weather app along with a graph to represent each days hourly stats</h4>
            </a>

            <a rel="noreferrer" href = "https://simpchess.com/forumapp" target="_blank" id = "forum" className={selected !== 6 ? `responsive__hidesidebox` : ""}>
                <h3>Forum Application</h3>
                <h4>Using my custom api you are able to log in, create posts, like posts, reply to posts and comments, edit and delete posts, and view other peopls profiles</h4>
            </a>

            <a rel="noreferrer" href = "https://simpchess.com/chessapp" target="_blank" id = "chess" className={selected !== 5 ? `responsive__hidesidebox` : ""}>
                <h3>Chess Application</h3>
                <h4>A completely working chess game with undo move capability</h4>
            </a>

            <i onClick = {() => handleClick("right")} className ="big icon angle right rightarrow"/>
            </div>);
    }

    return (
        <div>
            {layers()}
        </div>
    );
};

export default Projects;